import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormGroupDirective, Validators } from '@angular/forms';
import { InputObservable } from 'ng-zorro-antd/core/types';
import { NzMessageService } from 'ng-zorro-antd/message';
import { Subject } from 'rxjs';
import { AlertService } from 'src/app/services/alert.service';
import { BranchService } from 'src/app/services/branch/branch.service';
import { BranchDTO } from '../view-branch/view-branch.component';

@Component({
  selector: 'app-add-branch',
  templateUrl: './add-branch.component.html',
  styleUrls: ['./add-branch.component.css']
})
export class AddBranchComponent implements OnInit {
  @Input()branch?: Subject<BranchDTO>;
  constructor(private fb:FormBuilder,
    private service:BranchService,
    private alertService:AlertService,
    private messageService:NzMessageService) { }

  form:FormGroup;
  isEditMode=false;
  isLoading=false;
  formButtonText='';
  id:string="";

  generateForm(){
    this.form=this.fb.group({
      name:[null,Validators.required],
      address:[null,Validators.required],
      mainBranch:[false,Validators.required]
    });
  }
  get f (){

    return this.form.controls;
  }
  ngOnInit(): void {
    this.generateForm();

    if(this.branch){
      this.branch.subscribe(res=>{
        if(res){
          this.form.patchValue(res);
          this.isEditMode=true;
          this.formButtonText='Update Branch';
          this.id=res.id;
        }

      });
    }
    else{
      //Add Form Case
      this.isEditMode=false;
      this.formButtonText='Add Branch';

    }

  }

  handleSubmit(formDirective:FormGroupDirective){
    this.isLoading=true;
    if(!this.form.dirty){
      this.form.markAllAsTouched();
      this.isLoading=false;
    }
    else{

      var data = this.form.value as BranchDTO;
      if(!this.isEditMode){
        this.service.SaveBranch(data).subscribe(res=>{
          if(res.isSuccessfull){
            this.alertService.success("Branch successfully Created");
            this.service.GetBranches();
          }
          else{
            this.alertService.error("Internal Server Error");
          }
        });
      }
      this.isLoading=false;
    }
  }


}
