import { MapsAPILoader } from '@agm/core';
import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { Address } from 'ngx-google-places-autocomplete/objects/address';
import { BranchService } from 'src/app/services/branch/branch.service';

@Component({
  selector: 'app-agents',
  templateUrl: './agents.component.html',
  styleUrls: ['./agents.component.css']
})
export class AgentsComponent implements OnInit {
  isApiLoaded = false;
  branchId='';
  agentId='';
  returnUrl=this._router.url;
  fullUrl='';
  search:string="";
  list = [];
  dateRange:Date;
  options: any = {
    componentRestrictions: { country: 'USA' }
  }
  filter($event){
    console.log($event);
  }
  constructor(
    private mapsAPILoader: MapsAPILoader,
    private branchService:BranchService,
    private _router:ActivatedRoute,
    private router :Router,

  ) {

  }

  ngOnInit() {
    this._router.queryParams.subscribe( async params=>{
      this.branchId=params['branchCode'] || null;
      const date = new Date();
const currentMonth = date.getMonth() + 1;


    this.branchService.GetAgentRelatedBranchWithOpenBalanceByMonthOrDate(this.branchId,null,null,currentMonth).subscribe(res=>{
      console.log('agent api',res.dynamicResult);
      if(res.dynamicResult){
        this.list=[...res.dynamicResult as any[]];
      }

    })
    });
  }
  showSingleAgent(data){
    this.router.navigateByUrl(`/branch-management/agent-details?agentId=${data.agentId}&branchCode=${this.branchId}&returnUrl=${encodeURIComponent(window.location.href)}`)
  }
  handleAddressChange(address: Address) {
    console.log(address,'ye lo address');
    console.log(address.formatted_address)
    console.log(address.geometry.location.lat())
    console.log(address.geometry.location.lng())
  }

}
