import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, ViewChild, TemplateRef, Input, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { NzModalRef, NzModalService } from 'ng-zorro-antd/modal';
import { Observable, Subject } from 'rxjs';
import { ExcelPDFFormat } from 'src/app/Model/ExcelFormatter';
import { agentExcelFormat } from 'src/app/services/excel/defaultFormat';

@Component({
  selector: 'app-excel-configurator',
  templateUrl: './excel-configurator.component.html',
  styleUrls: ['./excel-configurator.component.css']
})
export class ExcelConfiguratorComponent implements OnInit {
  showFilter = false;
  form: FormGroup;
  formControlKeys: string[] = [];
  @Input() excelFormat:ExcelPDFFormat;

  excelFormatValue:any;

  public changedColumnsCount: number = 0;
  constructor(private modalService: NzModalService, private fb: FormBuilder) {

    this.createForm();

  }

  ngOnInit(): void {
    if(this.excelFormat){
      console.log(this.excelFormat);
    }

  }
  private createForm() {
    const controls = {};
    this.formControlKeys = []; // Reset the keys array

    for (const column in this.excelFormat) {
      if (this.excelFormat.hasOwnProperty(column)) {
        controls[column] = this.fb.group({
          visible: [this.excelFormat[column].visible],
          ordinalNum: [this.excelFormat[column].ordinalNum],
          displayName: [this.excelFormat[column].displayName]
        });

        this.formControlKeys.push(column); // Add the column key to the formControlKeys array
      }
    }

    this.form = this.fb.group(controls);
  }

  onDrop(event: CdkDragDrop<any[]>) {
    // Get the array of form control keys
    let formControlKeys = Object.keys(this.form.controls);

    // Check if the item was dropped in the same container
    if (event.previousContainer === event.container) {
      // Visually move the item in the container
      moveItemInArray(formControlKeys, event.previousIndex, event.currentIndex);
    }

    // Swap the ordinal numbers in the form controls
    const fromControl = this.form.get(formControlKeys[event.previousIndex]);
    const toControl = this.form.get(formControlKeys[event.currentIndex]);

    const fromOrdinalNum = fromControl.get('ordinalNum').value;
    const toOrdinalNum = toControl.get('ordinalNum').value;

    fromControl.get('ordinalNum').setValue(toOrdinalNum);
    toControl.get('ordinalNum').setValue(fromOrdinalNum);

    // Update the form with the new order of form control keys
    const newFormControls = {};
    formControlKeys.forEach((key) => {
      newFormControls[key] = this.form.get(key);
    });

    this.form = this.fb.group(newFormControls);
  }



  openModal() {

    this.showFilter = !this.showFilter;
    if(this.showFilter===false){

      this.modalService.closeAll();
    }
  }
  onSubmit($event) {
    $event.preventDefault();
    this.checkForChanges();
    this.modalService.closeAll();
    this.showFilter=!this.showFilter;
    if (this.form.valid) {
      // Here, you can use the form value to update the excelFormat object
      // Then, save the excelFormat object for later use or send it to the server
      console.log(this.form.value);

    }
  }
checkForChanges() {
  let changedColumns = 0;

  for (const key in this.excelFormat) {
    if (this.excelFormat.hasOwnProperty(key)) {
      const initialValue = this.excelFormat[key];
      const formValue = this.form.get(key).value;

      if (
        initialValue.ordinalNum !== formValue.ordinalNum ||
        initialValue.displayName !== formValue.displayName ||
        initialValue.visible !== formValue.visible
      ) {
        changedColumns++;
      }
    }
  }

  this.changedColumnsCount = changedColumns;
}



}
