import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import ChartistTooltip from 'chartist-plugin-tooltips-updated';
import * as feather from 'feather-icons';
import { DatePipe, Location } from '@angular/common';

import { FormArray, FormBuilder, FormControl, FormGroup } from '@angular/forms';

import { Statement } from 'src/app/Model/Statement';
import { BranchService } from 'src/app/services/branch/branch.service';
import { Subject } from 'rxjs';
import { AuthService } from 'src/app/services/auth.service';
import { UserService } from 'src/app/services/user/user.service';
import { User } from 'src/app/Model/User';
import { ExcelPDFFormat } from 'src/app/Model/ExcelFormatter';
@Component({
  selector: 'app-agent-details',
  templateUrl: './agent-details.component.html',
  styleUrls: ['./agent-details.component.css']
})
export class AgentDetailsComponent implements OnInit {
  excelFormat:ExcelPDFFormat = new ExcelPDFFormat();
  form:FormGroup;

  list:Statement[];
  columnConfiguration:Subject<any>= new Subject<any>();

heading='Agent Transactional History';
 agentId=null;
 branchCode=null;
 returnUrl = null;
 branches=[];
  constructor(private _router:ActivatedRoute,
    private router : Router,
    private auth:UserService,
    private fb:FormBuilder,
    private datePipe: DatePipe,
    private branchService:BranchService,
   private location: Location) {
    this.form=this.fb.group({
      from:[null],
      to:[null],
      requestExcel:[null],
      requestPDF:[null],
      branches:fb.array([])
    })
   }
    onFilterSelected(option: string) {
      // Handle filter selection

    }
  ngOnInit(): void {
    feather.replace();
    this._router.queryParams.subscribe( async params=>{

      this.agentId =params['agentId'] || null;
      this.branchCode = params['branchCode'] || null;
      this.returnUrl=decodeURIComponent( params['returnUrl']) || null;;
      if(this.agentId!=null && this.branchCode!=null){ //query Params check


          this.branchService.branchObserver$.subscribe(res=>{ //start of subscription of branch API
            if(res){
              this.branches= res.branches;




            }
          }); //end subscription of branchService
          this.branchService.GetBranches();
          this.auth.loggedInUserObservable$.subscribe(res=>{
            if(res){
              let user = res as User;
              console.log(res);
             let existingSettings =  localStorage.getItem(`${user.id}`);
             if(existingSettings){
              this.excelFormat= JSON.parse(existingSettings) as ExcelPDFFormat;
             }


            }
          });
          this.auth.GetLoginUser();
          console.log(this.excelFormat);
      }
      else{
        console.log(this.returnUrl)
       this.location.back();
      }
    })

  }
  get branchesArray() {
    return this.form.get('branches') as FormArray;
  }

  branchHandler(isChecked: any, branch: any) {
    // event.stopPropagation();
     const branchId = branch.id;
    const index = this.branchesArray.controls.findIndex(x => x.value === branchId);
    if(isChecked && index===-1){
      this.branchesArray.push(this.fb.control(branchId));
    }
    else{
        this.branchesArray.removeAt(index);
    }


  }
  branchDisplayName(id: any): string {
    if (this.branches) {
      const branch = this.branches.find(x => x.id === id.value);
      if (branch) {

        return branch.name;
      }
    }
    return '';
  }
  removeBranch(id){
    const branchId = id;
    const index = this.branchesArray.controls.findIndex(x => x.value === branchId);

    this.branchesArray.removeAt(index);

  }
  DoesBranchHasValue(branch: any):boolean {

    return this.branchesArray.controls.some(x => x.value === branch.id);
  }
  checked=false;
  search(){

  var from = this.form.controls.from.value as Date;
  var to = this.form.controls.to.value as Date;
  const formattedFromValue = this.datePipe.transform(from, 'yyyy-MM-ddTHH:mm:ss');
    const formattedToValue = this.datePipe.transform(to, 'yyyy-MM-ddTHH:mm:ss');
  var branches = this.form.controls.branches.value as string [];
    this.branchService.GetAgentTransactionalHistoryByBranches(this.agentId,formattedFromValue,formattedToValue,branches).subscribe(res=>{

      if(res?.dynamicResult){
        console.log(res.dynamicResult)
        this.list = res.dynamicResult.data;
      }

    });

  }
  goBack(): void {
    this.location.back();
  }
}
