import { Component, Renderer2, ElementRef , OnInit,AfterViewInit, ViewChild, Input  } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatDatepickerInputEvent } from '@angular/material/datepicker';
import { ActivatedRoute } from '@angular/router';

import { Observable, Subject, Subscription } from 'rxjs';
import { BranchService, SalesReport } from 'src/app/services/branch/branch.service';
@Component({
  selector: 'app-overview',
  templateUrl: './overview.component.html',
  styleUrls: ['./overview.component.css']
})
export class OverviewComponent implements OnInit,AfterViewInit  {
  toppingsControl = new FormControl();
  toppingList: string[] = ['Extra cheese', 'Mushroom', 'Onion', 'Pepperoni', 'Sausage', 'Tomato'];



  currentMonth = new Date().getMonth()+1;
  currentYear = new Date().getFullYear();
   getMonthName (id){
    return this.months.find(x=>x.value==id)?.name;
  }
  months = [
    { name: 'January', value: 1 },
    { name: 'February', value: 2 },
    { name: 'March', value: 3 },
    { name: 'April', value: 5 },
    { name: 'May', value: 5 },
    { name: 'June', value: 6 },
    { name: 'July', value: 7 },
    { name: 'August', value: 8 },
    { name: 'September', value: 9 },
    { name: 'October', value: 10 },
    { name: 'November', value: 11 },
    { name: 'December', value: 12 },
  ];
  selectedMonth: number;
  selectedYear: number;

  isPastMonth(month: number, year: number): boolean {
    return year < this.currentYear || (year === this.currentYear && month <= this.currentMonth);
  }

  isCurrentMonth(month: number, year: number): boolean {
    return year === this.currentYear && month === this.currentMonth;
  }

  isDisabled(month: number, year: number): boolean {
    return !this.isPastMonth(month, year) || !this.isCurrentMonth(month, year);
  }
  branchId='';
  branchCode='';
  agentReportResponse:SalesReport;
  agentARReportResponse:SalesReport;
  agentAPReportResponse:SalesReport;
  constructor(private renderer :Renderer2,private el:ElementRef,
    private _router:ActivatedRoute,
    private branchService:BranchService){
      this.selectedMonth = this.currentMonth;
      this.selectedYear = this.currentYear;
  }
  ngOnInit(): void {
    this._router.queryParams.subscribe( async params=>{
      this.branchId=params['branchCode'] || null;
      if(this.branchId!=null){
        let monthIndex: number = new Date().getMonth()+1;
        this.branchService.GetAgentBranchSales(monthIndex,this.branchId).subscribe(res=>{
          if(res.isSuccessfull){
            this.agentReportResponse=res.dynamicResult
              this.single=[
                ...this.agentReportResponse.salesAgents.map((agent) => ({
                  name: agent.name,
                  value: agent.amount,
                  percent:agent.percentage
                })),
              ]

          }
          console.log(res);
         });
         this.branchService.GetAgentARForBranchPerMonth(monthIndex,this.branchId).subscribe(res=>{
          if(res.isSuccessfull){
            this.agentARReportResponse=res.dynamicResult;
            this.singleForAR=[
              ...this.agentARReportResponse.salesAgents.map((agent)=> ({
                name:agent.name,
                value:agent.amount,
                percentage:agent.percentage
          })
               )
            ]
          }
         });

         this.branchService.GetBrokersAPForBranchPerMonth(monthIndex,this.branchId)
         .subscribe(res=>{
          if(res.isSuccessfull){

            this.agentAPReportResponse=res.dynamicResult;
            console.log('AP,',res.dynamicResult)
            this.singleForAP=[
              ...this.agentAPReportResponse?.details?.map((agent)=> ({
                name:agent.name,
                value:agent.amount,
                percentage:agent.percentage
          })
               )
            ]
          }
         });
      }
    });




  }
  ngAfterViewInit(): void {
    let monthIndex: number = new Date().getMonth()+1;
    this.branchService.GetAgentBranchSales(monthIndex,this.branchId).subscribe(res=>{
      if(res.isSuccessfull){
        this.agentReportResponse=res.dynamicResult
          this.single=[
            ...this.agentReportResponse.salesAgents.map((agent) => ({
              name: agent.name,
              value: agent.amount,
              percent:agent.percentage
            })),
          ]

      }
      console.log(res);
     });
  }
  changeMonth(){
    this.branchService.GetAgentBranchSales(this.currentMonth,this.branchId).subscribe(res=>{
      if(res.isSuccessfull){
        this.agentReportResponse=res.dynamicResult
          this.single=[
            ...this.agentReportResponse.salesAgents.map((agent) => ({
              name: agent.name,
              value: agent.amount,
              percent:agent.percentage
            })),
          ]

      }
      console.log(res);
     });
  }
  applyStylesToScrollbar() {
    const scrollTrack = this.el.nativeElement.querySelector('::-webkit-scrollbar-track');
    const scrollThumb = this.el.nativeElement.querySelector('::-webkit-scrollbar-thumb');

    this.renderer.setStyle(scrollTrack, 'background-color', '#fb0000');
    this.renderer.setStyle(scrollThumb, 'background-color', '#fb0000');

    scrollThumb.addEventListener('mouseover', () => {
      this.renderer.setStyle(scrollThumb, 'background-color', 'fb0001');
    });

    scrollThumb.addEventListener('mouseout', () => {
      this.renderer.setStyle(scrollThumb, 'background-color', '#fb0000');
    });
  }


  response = {
    branchName: '',
    totalSales: 0,
    agentsByPercent: [

    ],
  };
  responseAR={
    branchName: '',
    totalSales: 0,
    agentsByPercent: [

    ],
  }
  responseAP={
    branchName: '',
    totalSales: 0,
    agentsByPercent: [

    ],
  }
  responseForBranchComparison={
    branchName: 'All Branches',
    totalReceviable: 850000,
    branchesByPercent: [
      {
        name: 'Main Office',
        value: 50,
      },
      {
        name: 'Main Office',
        value: 50,
      },

    ],
  };

  // Define your branded color and opacity
  brandColor = '#0096d6';
  brandOpacity = 0.8;

  receviableColor='#20c997' //cyan
  receviableOpacity=0.8
  apColor='#fd7e14' // orange
  apOpacity=0.8
 branchComparisonColor='#2ed47a'
 branchComparisonOpacity=0.8
  single = [


  ];
  singleForAR = [


  ];
  singleForAP = [


  ];
  singleForBranchComparison = [


  ];

  colorScheme = {
    domain: [
      this.brandColor,
      `${this.brandColor}${Math.floor(this.brandOpacity * 255).toString(16)}`,
      `${this.brandColor}${Math.floor(this.brandOpacity * 200).toString(16)}`,
      `${this.brandColor}${Math.floor(this.brandOpacity * 150).toString(16)}`,
      `${this.brandColor}${Math.floor(this.brandOpacity * 100).toString(16)}`,
      `${this.brandColor}${Math.floor(this.brandOpacity * 50).toString(16)}`,
    ],
  };
  colorSchemeForAR = {
    domain: [
      this.receviableColor,
      `${this.receviableColor}${Math.floor(this.receviableOpacity * 255).toString(16)}`,
      `${this.receviableColor}${Math.floor(this.receviableOpacity * 200).toString(16)}`,
      `${this.receviableColor}${Math.floor(this.receviableOpacity * 150).toString(16)}`,
      `${this.receviableColor}${Math.floor(this.receviableOpacity * 100).toString(16)}`,
      `${this.receviableColor}${Math.floor(this.receviableOpacity * 50).toString(16)}`,
    ],
  };
  branchComparisonForSales= {
    domain: [
      this.branchComparisonColor,
      `${this.branchComparisonColor}${Math.floor(this.branchComparisonOpacity * 255).toString(16)}`,
      `${this.branchComparisonColor}${Math.floor(this.branchComparisonOpacity * 200).toString(16)}`,
      `${this.branchComparisonColor}${Math.floor(this.branchComparisonOpacity * 150).toString(16)}`,
      `${this.branchComparisonColor}${Math.floor(this.branchComparisonOpacity * 100).toString(16)}`,
      `${this.branchComparisonColor}${Math.floor(this.branchComparisonOpacity * 50).toString(16)}`,
    ],
  };
  colorSchemeForAP= {
    domain: [
      this.apColor,
      `${this.apColor}${Math.floor(this.apOpacity * 255).toString(16)}`,
      `${this.apColor}${Math.floor(this.apOpacity * 200).toString(16)}`,
      `${this.apColor}${Math.floor(this.apOpacity * 150).toString(16)}`,
      `${this.apColor}${Math.floor(this.apOpacity * 100).toString(16)}`,
      `${this.apColor}${Math.floor(this.apOpacity * 50).toString(16)}`,
    ],
  };
}





