export class Column {
  ordinalNum: number;
  displayName: string;
  visible: boolean;

  constructor(ordinalNum: number, displayName: string, visible: boolean) {
    this.ordinalNum = ordinalNum;
    this.displayName = displayName;
    this.visible = visible;
  }
}
export class ExcelPDFFormat {
  date: Column;
  customerName: Column;
  policyNumber: Column;
  companyName: Column;
  grossPremium: Column;
  commission: Column;
  referenceNumber: Column;
  vehicleType: Column;
  motorType: Column;
  remark: Column;
  policyIssuer: Column;
  status: Column;
  debit: Column;
  credit: Column;

  constructor() {
    this.date = new Column(1, 'Date', true);
    this.customerName = new Column(2, 'Customer Name', true);
    this.policyNumber = new Column(3, 'Policy Number', true);
    this.companyName = new Column(4, 'Company Name', true);
    this.grossPremium = new Column(5, 'Gross Premium', true);
    this.commission = new Column(6, 'Commission', true);
    this.referenceNumber = new Column(7, 'Reference Number', true);
    this.vehicleType = new Column(8, 'Vehicle Type', true);
    this.motorType = new Column(9, 'Motor Type', true);
    this.remark = new Column(10, 'Remark', true);
    this.policyIssuer = new Column(11, 'Policy Issuer', true);
    this.status = new Column(12, 'Status', true);
    this.debit = new Column(13, 'Debit', true);
    this.credit = new Column(14, 'Credit', true);
  }
}
