import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, RouterLink, RouterStateSnapshot } from '@angular/router';
import { NzPlacementType } from 'ng-zorro-antd/dropdown';
import { BranchDetails, BranchService } from 'src/app/services/branch/branch.service';

@Component({
  selector: 'app-details',
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.css']
})
export class DetailsComponent implements OnInit {

  constructor(private _router:ActivatedRoute,
    private _service:BranchService,
    ) { }


    listOfPosition: NzPlacementType[] = ['bottomLeft', 'bottomCenter', 'bottomRight', 'topLeft', 'topCenter', 'topRight'];
    welcomeText="/Branch Management"
pageTitle="BRANCH_NAME";
address="PLACE_HOLDER_ADDRESS";
totalExpense=0;
Employees=0;
PointOfContactPerson="";
email="placeholder@addemail.com";
salesCount=0;
agentsCount =0;
branchManager="";
BranchDetails:BranchDetails;
branchId:string="";
 async ngOnInit():Promise<any> {
    this._router.queryParams.subscribe( async params=>{
      this.branchId=params['branchCode'] || null;
      if(this.branchId!=null){

        this._service.branchDetailsObserver$.subscribe(res=>{
          this.BranchDetails=res;
          console.log('detail dekho',res)
        });
        this._service.GetBranchDetailsAsync(new Date().getMonth(),this.branchId);
        //NgOnInit Starts Actually From here


      }
      else{
        window.location.href="/#/branch-management"
      }
    })
  }

}
