import { Component, OnInit } from '@angular/core';
import { Router, RouterLink } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd/message';

import { Agent } from 'src/app/Model/Agent';
import { BranchService } from 'src/app/services/branch/branch.service';

@Component({
  selector: 'app-view-branch',
  templateUrl: './view-branch.component.html',
  styleUrls: ['./view-branch.component.scss']

})
export class ViewBranchComponent implements OnInit {
  listData:BranchDTO[];

  _receivables:DifferentialStats;
 get receivables () {
return this._receivables;
 }

 _cashInBanks:DifferentialStats;
  get cashInBanks () {
    return this._cashInBanks;
     }
  _payables:DifferentialStats;
  get payables () {
    return this._payables;
     }
  _expenses:DifferentialStats;
  get expenses () {

    return this._expenses;
     }
  branchCount:number =0;
  loading=false;
  constructor(private service:BranchService,

    private _router:Router,
    private messageService:NzMessageService) {



   }
  title='Create Your New Branch';

  isVisible=false;
  ngOnInit(): void {
    this.loading=true;
 //   this.messageService.loading('loading');
    this.service.GetBranchAsync().subscribe(res=>{

      if(res.isSuccessfull){
        const response = res.dynamicResult;
        this.isVisible=false;
        this.listData=response?.branches;
        this.loading=false;

        this._payables=response?.statsForAP;
        this._receivables=response?.statsForAR;
        this._cashInBanks=response?.statsForCASH;
        this._expenses=response?.statsForExpense;
        this.branchCount=response?.branchCount;
        console.log(response);
      }

    });
    this.service.GetBranches();
    this.listData=this.service.branchesWithDetail?.branches;

  }
  showMessage(){


  }
  hideMessage(){
  }
  showModal(){

  }

  NavigateToBranch(data: any) {
    // [routerLink]="'branch-management/details'"

    window.location.href="/#/branch-management/details?branchCode="+data?.id;
    // let id = data.id;
    // if (id) {
    //   this._router.navigate(['/details']);

    // }
  }
 }
export class BranchDTO{
  id:string;
  name:string;
  address:string;
  mainBranch:boolean

}
export class BranchWithDetail{
  statsForAR:DifferentialStats;
  statsForCASH:DifferentialStats;
  statsForAP:DifferentialStats;
  statsForExpense:DifferentialStats;
  createdDate:Date;
  branches:BranchDTO[];
  branchCount:number;
}
export class DifferentialStats{

   statsFor :StatsFor;
  increaseOrDecrease :boolean;
 public get percentage(){
  return this.percentage.toFixed(2);
 }

 current :number
  last :number
}
export enum StatsFor{
  ReceviableMonth=1,

  PayableMonth,

  CashInBankMonth,

  ExpenseMonth,
}
