import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormComponent } from './form/form.component';
import { ExcelConfiguratorComponent } from './excel-configurator/excel-configurator.component';
import {MatIconModule} from '@angular/material/icon';
import { MatButtonModule } from '@angular/material/button';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatToolbarModule } from '@angular/material/toolbar';
import { MatSidenavModule } from '@angular/material/sidenav';
import { AntdModule } from 'src/app/antd/antd.module';
import { ReactiveFormsModule  } from '@angular/forms';
import { SharedModule } from 'src/app/shared/shared.module';
import { DragDropModule } from '@angular/cdk/drag-drop';
import { SortColumnsPipe } from './excel-configurator/sort-columns.pipe';


@NgModule({
  declarations: [FormComponent, ExcelConfiguratorComponent, SortColumnsPipe],
  imports: [
    CommonModule,
    MatIconModule,
    MatButtonModule,
    MatTooltipModule,
    AntdModule,
    ReactiveFormsModule ,
    SharedModule,
    DragDropModule
  ],
  exports:[ExcelConfiguratorComponent]
})
export class GenericModule { }
